
@mixin breakpoint-small {
    @media screen and (min-width: 769px) {
      @content;
    }
  }

@mixin breakpoint-large {
    @media screen and (min-width: 1300px) {
      @content;
    }
  }
@mixin display-flex-centered {
    display: flex;
    align-items: center;
    justify-content: center;
}